<template>
  <v-container fluid>
    <v-card class="pa-3">
      <v-text-field v-model="title" label="Title" counter> </v-text-field>
      <v-textarea
        clearable
        counter
        outlined
        v-model="description"
        label="Description"
        rows="12"
      ></v-textarea>
      <v-card-actions>
        <v-btn class="success mx-3" @click="saveCategory">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
import Category from "@/model/category";
import CategoryService from "@/service/category_service.js";

export default {
  name: "create-category",
  data: () => ({
    title: "",
    description: "",
    //
  }),
  methods: {
    saveCategory() {
      let category = new Category(this.title.trim(), this.description);
      if (category.title) {
        CategoryService.exists(category.title)
          .then((result) => {
            console.log(result);
            if (!result) {
              CategoryService.create(category)
                .then((resp) => {
                  this.title = "";
                  this.description = "";
                })
                .catch((error) => {
                  console.log(error);
                });
            } else {
              console.log("the title is exists.");
            }
          })
          .catch((error) => {
            //
          });
      } else {
        console.log("the title is null.");
      }
    },
  },
};
</script>

